/* global google */
import React, { Component } from 'react'
import Link from 'gatsby-link'

class Contact extends Component {
  componentDidMount() {
    var mapCanvas = document.getElementById('map-canvas');
    var mapOptions = {
      center: new google.maps.LatLng(26.614726, 91.178906),
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP
    }

    var map = new google.maps.Map(mapCanvas, mapOptions);

    var myLatLng = {lat: 26.614726, lng: 91.178906};

    var marker = new google.maps.Marker({
      position: myLatLng,
      map: map,
      title: 'North Kamrup College',
      label: 'North Kamrup College',
    });
  }

  render() {
    return (
      <div>
        <section className="nkc-banner hidden-print"></section>
        <main className="nkc-container hidden-print clearfix">
          <section className="breadcumb hidden-print">
            <div className="container clearfix">
              <div className="col-lg-full col-md-full col-sm-full">
                <p><Link to="/">Home</Link> &rarr; Contact</p>
              </div>
            </div>
          </section>
          <div className="clearfix hidden-print">
            <div className="col-lg-large col-md-large col-sm-full col-print-large clearfix">
              <div className="nkc-container__content clearfix">
                <h1 className="nkc-page-title" id="the-college">Contact</h1>
                <p className="nkc-para-intro">Question, comment or concern? We welcome communications relating to North Kamrup College. Feel free to get in touch with us. We will respond to you as soon as possible.</p>
                <h2>Way to North Kamrup College</h2>
                <p>Below you can find a quick roadmap to get to our college.</p>
                <script src="./Contact — North Kamrup College_files/googlemap.js"></script>
                <section className="google-map" id="map-canvas"></section>
                <form className="get-directions" action="http://maps.google.com/maps" method="get" target="_blank"><input type="text" name="saddr" placeholder="Enter your starting location" /><input type="hidden" name="daddr" value="North Kamrup College, Baghmara Bazar, Pub Kathal Muri, Assam 781328" /><input data-role="button" className="ui-half-width" type="submit" value="Get directions" /></form>
              </div>
            </div>
            <div className="col-lg-medium col-md-medium col-sm-full col-print-medium">
              <div className="nkc-container__sidebar">
                <div className="responsive-table">
                  <table className="table-no-border nkc-feature-table">
                    <tbody>
                      <tr>
                        <td colSpan="2">
                          <h2><span className="fa fa-fw fa-bookmark-o"></span> Contact Details</h2>
                        </td>
                      </tr>
                      <tr>
                        <td>Office</td>
                        <td>North Kamrup College, Baghmara, Barpeta
                          Assam, India, 781328
                        </td>
                      </tr>
                      <tr>
                        <td>Telephone</td>
                        <td><a href="tel:+913666268747">+91 366 626 8747</a></td>
                      </tr>
                      <tr>
                        <td>Email</td>
                        <td><a href="mailto:contact@northkamrupcollege.in">Official Email</a></td>
                      </tr>
                      <tr>
                        <td>Website</td>
                        <td><Link to="/" title="Official Website of North Kamrup College">Official Website</Link></td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </main>
        <div className="spacer"></div>
      </div>
    )
  }
}

export default Contact



